import DashboardLayout from "./admin/layout/DashboardLayout"
import Dashboard from "./admin/pages/Dashboard"
import PatientsTable from "./admin/pages/PatientsTable"
import ViewPatient from "./admin/pages/ViewPatient"
import { Route, Routes } from "react-router-dom"
import PatientDetailForm from "./pages/PatientDetailForm"
import LoginPage from "./pages/LoginPage"
import AddDoctorPrescription from "./pages/AddDoctorPrescription"
import Contact from "./Contact";

const AllRoutes = () =>{
    return (
<Routes>
  <Route
    path="/"
    element={<Contact/>}
  />
  <Route
    path="/pms/v1/demo/login"
    element={<LoginPage/>}
  />

  <Route
    path="/pms/v1/demo/admin"
    element={<DashboardLayout><Dashboard /></DashboardLayout>}
  />

  <Route
    path="/pms/v1/demo/admin/patients"
    element={<DashboardLayout><PatientsTable /></DashboardLayout>}
  />

  <Route
    path="/pms/v1/demo/admin/patient/:id"
    element={<DashboardLayout><ViewPatient /></DashboardLayout>}
  />

  <Route
    path="/pms/v1/demo/addpatient"
    element={<DashboardLayout><PatientDetailForm /></DashboardLayout>}
  />
  <Route
  path="/pms/v1/demo/admin/patient/:id/prescription"
  element={<AddDoctorPrescription />}
/>

</Routes>
    )

}

export default AllRoutes;