import React, { useState } from "react";
import {
  Alert,
  Button,
  TextField,
  MenuItem,
  Select,
  InputLabel,
  FormControl,
  Box,
} from "@mui/material";
import "./style.css";
import { useNavigate } from "react-router-dom";

export default function Contact() {
  const [form, setForm] = useState({
    reason: "",
    name: "",
    phone: "",
    email: "",
    message: "",
  });
  const navigate = useNavigate();
  const [alertMsg, setAlertMsg] = useState("");
  const [loading, setLoading] = useState(false); // button disable control

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true); // disable button

    const res = await fetch("/api/contact", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(form),
    });

    const data = await res.json();
    setAlertMsg(data.message);

    // Clear form fields
    setForm({
      reason: "",
      name: "",
      phone: "",
      email: "",
      message: "",
    });

    setLoading(false); // enable button again

    // Auto hide alert after 10 seconds
    setTimeout(() => {
      setAlertMsg("");
    }, 10000);
  };

  return (
    <div className="main-wrapper">
      {/* Left side */}
      <div className="left-section">
        <img src="/logo02.jpg" alt="Logo" className="logo" />

        <div className="left-text">
          <h1 className="title">We are coming soon</h1>
          <p className="subtitle">
            To be a partner built for your business growth journey.
          </p>
        </div>
        <div style={{ display: "flex", margin:'auto' }}>
            <Button
            onClick={() => navigate("/pms/v1/demo/login")}
            disabled={loading} 
            style={{ width: "20%" }}
            sx={{
              backgroundColor: loading ? "#999" : "black",
              padding: "10px",
              // fontSize: "16px",
              ":hover": { backgroundColor: loading ? "#999" : "#333" },
            }}
          >
            Demo
          </Button>
          </div>
      </div>

      <div className="divider"></div>

      {/* Right side */}
      <div className="right-section">
        <h2 className="contact-title">CONTACT US</h2>

        {alertMsg && (
          <Alert severity="success" sx={{ mb: 2 }}>
            {alertMsg}
          </Alert>
        )}

        <Box component="form" onSubmit={handleSubmit} sx={{ mt: 2 }}>
          {/* Dropdown */}
          <FormControl style={{ marginTop: "15px" }} fullWidth sx={{ mb: 2 }}>
            <InputLabel id="reason-label">
              I WOULD LIKE TO CONNECT FOR
            </InputLabel>
            <Select
              labelId="reason-label"
              label="I WOULD LIKE TO CONNECT FOR"
              name="reason"
              value={form.reason}
              onChange={handleChange}
              required
            >
              <MenuItem value="Initial Consultation">
                Initial Consultation
              </MenuItem>
              <MenuItem value="Sharing Vision">Sharing Vision</MenuItem>
              <MenuItem value="Looking Partner for Business Growth">
                Looking Partner for Business Growth
              </MenuItem>
            </Select>
          </FormControl>

          {/* Name */}
          <TextField
            fullWidth
            label="Name"
            name="name"
            value={form.name}
            onChange={handleChange}
            required
            sx={{ mb: 2 }}
          />

          {/* Phone */}
          <TextField
            fullWidth
            label="Phone"
            name="phone"
            value={form.phone}
            onChange={handleChange}
            required
            sx={{ mb: 2 }}
          />

          {/* Email */}
          <TextField
            fullWidth
            label="Email Id"
            type="email"
            name="email"
            value={form.email}
            onChange={handleChange}
            required
            sx={{ mb: 2 }}
          />

          {/* Message */}
          <TextField
            fullWidth
            label="Message"
            name="message"
            multiline
            rows={4}
            value={form.message}
            onChange={handleChange}
            required
            sx={{
              mb: 2,
              "& textarea": {
                minHeight: "10px",
              },
            }}
          />

          <Button
            type="submit"
            variant="contained"
            fullWidth
            disabled={loading} // disable button
            sx={{
              backgroundColor: loading ? "#999" : "black",
              padding: "10px",
              fontSize: "16px",
              ":hover": { backgroundColor: loading ? "#999" : "#333" },
            }}
          >
            {loading ? "Sending..." : "Send Message"}
          </Button>
        </Box>
      </div>
    </div>
  );
}
